*,
*::after,
*::before {
    margin: 0;
    padding: 0;
    box-sizing: inherit;
}

html {
    box-sizing: border-box;
    font-size: 50%;
}

body {
    font-size: 1.6rem;
    font-family: 'Open Sans', sans-serif;
    height: 100%;
}

.content {
    min-height: calc(100vh - 10rem);
    position: relative;
}

h1 {
    margin: 0;
}

.row {
    max-width: 114rem;
    margin: 0 auto;
    padding: 0;
}

@media (min-width: $large-phone) {
    html { font-size: 62.5%; }
}